@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body{background:#f2f2f2;font-family:'Open Sans', sans-serif}.search{width:100%;position:relative;display:flex}.searchTerm{width:100%;border:3px solid #00B4CC;border-right:none;padding:5px;border-radius:5px 0 0 5px;outline:none;color:#9DBFAF}.searchTerm:focus{color:#00B4CC}.searchButton{width:25%;border:1px solid #00B4CC;background:#00B4CC;text-align:center;color:#fff;border-radius:0 5px 5px 0;cursor:pointer;font-size:20px}.wrap{width:30%;position:absolute;left:50%;-webkit-transform:translate(-50%, 0%);transform:translate(-50%, 0%)}.weather-card{margin:20px 5px;padding:5px;border-radius:20px;position:absolute;top:25%;left:49%;overflow:hidden;width:270px;max-width:300px;height:300px;background-color:#fff;box-shadow:0px 0px 25px 1px rgba(50,50,50,0.1);text-align:center;justify-self:center}.heading{text-align:center;font-weight:bold;font-size:4rem;letter-spacing:0.02em;padding:0 0 30px 0;color:#00B4CC}@media only screen and (max-width: 1024px){.searchButton{width:40%}.wrap{width:55%}}@media only screen and (max-width: 768px){.heading{font-size:3rem}}@media only screen and (max-width: 568px){.heading{font-size:2.5rem}}

